import {
  Checkbox,
  CheckboxProps,
  FormControl,
  FormControlLabel,
  FormControlLabelProps,
  FormHelperText
} from '@mui/material';
import React, { ReactNode, memo } from 'react';

export interface CheckboxInputProps
  extends Omit<CheckboxProps, 'name' | 'error' | 'value' | 'checked'> {
  /**
   * The checkbox value.
   */
  readonly value: boolean;

  /**
   * The label content.
   */
  readonly label: ReactNode;

  /**
   * MUI FormControlLabel props.
   */
  readonly labelProps?: Omit<FormControlLabelProps, 'control' | 'label'>;

  /**
   * The helper text content.
   */
  readonly helperText?: ReactNode;

  /**
   * The error message.
   */
  readonly error?: string;
}

/**
 * Material-UI Checkbox with label and error helper text.
 */
export const CheckboxInput = memo((props: CheckboxInputProps) => {
  const { value, error, label, helperText, labelProps, ...muiProps } = props;

  const normalizedValue = value ?? false;

  return (
    <FormControl error={error != null}>
      <FormControlLabel
        label={label}
        control={
          <Checkbox
            {...muiProps}
            value={normalizedValue}
            checked={normalizedValue}
            onChange={(event, checked) => {
              muiProps.onChange?.(event, checked);
            }}
            onBlur={event => {
              muiProps.onBlur?.(event);
            }}
          />
        }
        {...labelProps}
      />

      {(error != null || helperText != null) && (
        <FormHelperText>{error ?? helperText}</FormHelperText>
      )}
    </FormControl>
  );
});
